<template>
  <div>
    <top-nav v-if="!this.edit_mode" :activeNew="true" />
    <top-nav v-if="this.edit_mode" :activeEdit="true" />

    <b-card no-body style="border-top: none;">
      <b-card-body>
        <form @submit="createTool">
          <b-row>
            <b-form-group class="col-8" label="Title" label-for="elementsAppend">
              <b-input-group>
                <b-form-input required type="text" v-model="form.title"></b-form-input>
              </b-input-group>
            </b-form-group>

            <b-form-group class="col-4" label="Tool Type" label-for="elementsAppend">
              <b-form-select required v-model="form.category" class="is-valid">
                <option value selected>---</option>
                <option value="link">Link</option>
                <option value="document">Document</option>
                <option value="video">HFS ACADEMY</option>
                <option value="post">Post</option>
                <option value="flayer">Flayer</option>
              </b-form-select>
            </b-form-group>
          </b-row>

          <b-row>
            <b-form-group class="col-12" label="Post/new" label-for="elementsAppend">
              <div v-if="form.category == 'post'">
                <b-input-group>
                  <vue-trix
                    class="col-12"
                    rows="6"
                    v-model="form.resource"
                    placeholder="Enter content"
                    localStorage
                  />
                </b-input-group>
              </div>

              <div v-if="form.category == 'link'">
                <b-input-group>
                  <b-form-input type="text" v-model="form.resource"></b-form-input>
                </b-input-group>
              </div>

              <div v-if="form.category == 'document' || form.category == 'flayer'">
                <b-input-group>
                  <b-form-file
                    placeholder="Choose a file..."
                    id="toolDocument"
                    type="file"
                    v-model="form.resource"
                    required
                  ></b-form-file>
                </b-input-group>
              </div>

              <div v-if="form.category == 'video'">
                <b-input-group>
                  <b-form-textarea
                    id="textarea"
                    v-model="form.resource"
                    placeholder="Enter iframe code.."
                    rows="5"
                    max-rows="6"
                    class="mb-3"
                  />
                </b-input-group>
                <small >Iframe Example:</small>
                <br>
                <code>&lt;iframe   src="https://www.youtube.com/embed/J0cWxtkiB20?list=PLdoh5VD8MSWudyWQTbxwOzjz6gEMlJN5s"  frameborder="0" style="position:absolute;top:0;left:0;width:100%;height:100%;" allowfullscreen&gt;&lt;/iframe&gt;</code>
              </div>

              <div v-if="form.category == 'document' || form.category == 'flayer'">
                <b-input-group>
                  <a v-bind:href="form.link_resource" target="_blank">View document</a>
                </b-input-group>
              </div>
            </b-form-group>
          </b-row>

          <b-row v-if="form.category == 'post'">
            <b-form-group class="col-12" label="Post file" label-for="elementsAppend">
              <b-input-group>
                <b-form-file
                  placeholder="Choose a file..."
                  id="toolDocument"
                  type="file"
                  v-model="form.post_file"
                ></b-form-file>
              </b-input-group>

              <b-input-group>
                <a v-bind:href="form.link_resource" target="_blank">View document</a>
              </b-input-group>
            </b-form-group>
          </b-row>

          <b-row v-if="form.category == 'document'">
            <b-form-group class="col-12" label="Preview" label-for="elementsAppend">
              <b-input-group>
                <b-form-file
                  placeholder="Choose a file..."
                  id="toolDocumentPreview"
                  type="file"
                  v-model="form.preview_file"
                ></b-form-file>
              </b-input-group>

              <b-input-group>
                <a v-bind:href="form.preview_url" target="_blank">View preview</a>
              </b-input-group>
            </b-form-group>
          </b-row>
          <hr />

          <div class="form-actions">
            <div v-if="this.edit_mode">
              <b-button v-if="!loading" type="submit" variant="primary mr-2">
                Save changes
                <i class="icon-cloud-upload" />
              </b-button>
              <b-button v-if="loading" variant="primary mr-2" disabled>
                Loading
                <i class="fa fa-spinner fa-spin" />
              </b-button>
              <b-button type="button" variant="danger" disabled>Delete</b-button>
            </div>
            <div v-else>
              <b-button v-if="!loading" type="submit" variant="warning mr-2">Create Now!</b-button>
              <b-button v-if="loading" variant="warning mr-2" disabled>
                Loading
                <i class="fa fa-spinner fa-spin" />
              </b-button>
              <b-button type="button" variant="secondary" @click="reset">Reset</b-button>
            </div>
          </div>
        </form>
      </b-card-body>
    </b-card>
  </div>
</template>

<script>
import NavTop from "./../Components/NavTop";
import { serverBus } from "@/main";

import Vue from "vue";
import _ from "lodash";
import VueTrix from "vue-trix";

export default {
  name: "InventoryNew",
  components: {
    "top-nav": NavTop,
    "vue-trix": VueTrix,
  },

  data() {
    return {
      tool_uuid: false,
      edit_mode: false,
      loading: false,
      form: {},
    };
  },

  created() {
    if (this.$route.params.uuid) {
      this.edit_mode = true;
      this.tool_uuid = this.$route.params.uuid;
      this.fetchTool(this.$route.params.uuid);
    }
  },

  methods: {
    fetchTool(uuid) {
      this.loading = true;
      this.$http(`/api/v1/admin/tools/${uuid}`, {
        headers: {
          "content-type": "application/json",
          Authorization: this.$store.getters.authToken,
        },
      })
        .then((response) => {
          this.form = response.data;
          this.loading = false;
        })
        .catch((error) => {
          console.log(error.response);
          this.loading = false;
        });
    },

    createTool(evt) {
      evt.preventDefault();
      let url;
      let method = this.edit_mode ? "PUT" : "POST";
      if (!this.edit_mode) url = `/api/v1/admin/tools`;
      if (this.edit_mode) url = `/api/v1/admin/tools/${this.form.id}`;

      this.loading = true;
      let data = new FormData();
      data.append("title", this.form.title);
      data.append("category", this.form.category);
      data.append("resource", this.form.resource);

      if (
        document.getElementById("toolDocument") &&
        document.getElementById("toolDocument").files.length > 0
      ) {
        data.append("file", document.getElementById("toolDocument").files[0]);
      }

      if (
        document.getElementById("toolDocumentPreview") &&
        document.getElementById("toolDocumentPreview").files.length > 0
      ) {
        data.append(
          "preview_file",
          document.getElementById("toolDocumentPreview").files[0]
        );
      }

      this.$http({
        url: url,
        data: data,
        method: method,
        headers: {
          "content-type": "multipart/form-data",
          Authorization: this.$store.getters.authToken,
        },
      })
        .then((response) => {
          let msg = this.edit_mode
            ? "Tool successfully updated"
            : "Tool successfully created";
          this.$toasted.show(msg, {
            position: "top-right",
            duration: 5000,
            type: "default",
            closeOnSwipe: true,
          });
          if (!this.edit_mode) this.reset();
          this.loading = false;
        })
        .catch((error) => {
          error.response.data.map((m) => {
            this.$toasted.show(m, {
              position: "top-right",
              duration: 5000,
              type: "error",
              closeOnSwipe: true,
            });
            this.loading = false;
          });
        });
    },

    reset() {
      this.form = {
        publish: true,
      };
    },
  },
};
</script>
